@import "./variables.css";

.navbar {
   position: fixed;
   top: 0;
   left: 50%;
   transform: translateX(-50%);
   width: 100vw;
   max-width: 1200px;
   height: 62px;
   background: rgba(10, 12, 26, 0.95);
   backdrop-filter: blur(10px);
   display: flex;
   justify-content: space-between;
   align-items: center;
   z-index: 1200;
   box-shadow: 0 0 15px var(--my-color);
   padding: 0 32px;
   border-radius: 0 0 18px 18px;
   border-bottom: 1.5px solid var(--my-color);
   transition: background 0.3s, box-shadow 0.3s;
}

.left-column-navbar {
   display: flex;
   align-items: center;
}

.left-column-navbar span {
   font-size: 1.3rem;
   color: var(--my-color);
   font-weight: bold;
   text-shadow: 0 0 8px var(--my-color);
   display: flex;
   align-items: center;
   letter-spacing: 1.5px;
}

.left-column-navbar span img {
   width: 40px;
   height: 40px;
   border-radius: 50%;
   margin-right: 12px;
   border: 2px solid var(--my-color);
   box-shadow: 0 0 12px var(--my-color);
   background: #fff;
   object-fit: cover;
}

.right-column-navbar {
   display: flex;
   align-items: center;
}

.right-column-navbar ul {
   list-style: none;
   display: flex;
   align-items: center;
   margin: 0;
   padding: 0;
}

.navbar li {
   margin: 0 auto;
   border-radius: 8px;
   transition: background 0.2s;
}

.navbar a {
   display: flex;
   align-items: center;
   color: white;
   text-decoration: none;
   font-weight: 500;
   font-size: 1.1rem;
   padding: 8px 16px;
   margin-left: 6px;
   border-radius: 8px;
   transition: 0.2s;
   letter-spacing: 0.5px;
   gap: 8px;
   position: relative;
}

.navbar a:hover,
.navbar a:focus {
   color: var(--my-color);
   background: rgba(0, 40, 90, 0.2);
   box-shadow: 0 0 15px var(--my-color);
   text-shadow: 0 0 8px var(--my-color);
}

.nav-icon {
   width: 21px;
   height: 21px;
   margin: 0 auto;
   vertical-align: middle;
   filter: drop-shadow(0 0 3px var(--my-color));
}

.nav-text {
   text-transform: capitalize;
}

/* Hamburger icon */
.hamburger {
   display: none;
   font-size: 28px;
   color: var(--my-color);
   cursor: pointer;
   z-index: 1221;
   transition: color 0.2s, transform 0.2s;
}

.hamburger:hover {
   color: #fff;
   transform: scale(1.1) rotate(10deg);
}

/* Overlay for mobile menu */
.navbar-overlay {
   position: fixed;
   top: 0;
   left: 0;
   width: 100vw;
   height: 100vh;
   background: rgba(10, 10, 20, 0.65);
   z-index: 1201;
}

/* --- Responsive --- */
@media (max-width: 900px) {
   .navbar {
      padding: 0 12px;
      max-width: 100vw;
   }
}

@media (max-width: 768px) {
   .hamburger {
      display: block;
      padding-right: 10px;
   }

   .left-column-navbar {
      padding-left: 10px;
   }

   .right-column-navbar {
      position: fixed;
      top: 0;
      right: 0;
      width: 150px;
      max-width: 370px;
      height: 100vh;
      background: rgba(10, 12, 26, 0.99);
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      padding: 80px 50px;
      gap: 12px;
      transform: translateX(100%);
      transition: transform 0.33s cubic-bezier(0.77, 0, 0.18, 1);
      z-index: 1222;
   }

   .right-column-navbar.open {
      transform: translateX(0);
      display: flex;
   }

   .right-column-navbar ul {
      flex-direction: column;
      align-items: flex-end;
      width: 100%;
   }

   .right-column-navbar li {
      margin: 12px 0;
      width: 100%;
   }

   .nav-icon {
      width: 24px;
      height: 24px;
      margin: 0 auto;
      vertical-align: middle;
      justify-content: flex-start;
      filter: drop-shadow(0 0 3px var(--my-color));
   }

   .navbar a {
      width: 100%;
      justify-content: flex-start;
      font-size: 1.07rem;
      padding: 10px 0;
   }
}

@media (max-width: 520px) {
   .navbar {
      border-radius: 0;
      max-width: 100vw;
      padding: 0 3vw;
   }

   .left-column-navbar span {
      font-size: 1.02rem;
   }

   .left-column-navbar span img {
      width: 30px;
      height: 30px;
      margin-right: 7px;
   }
}
