@import "../css/variables.css";

.contact-container {
   width: 1200px;
   margin: 0 auto;
   padding: 40px 20px;
   justify-content: left;
   gap: 20px;
}

.contact-tittle-container {
   display: inline-block;
   flex-direction: column;
   align-items: center;
   width: 100%;
}

.contact-title {
   font-size: 2.5rem;
   text-align: left;
   color: var(--my-color);
   font-weight: bold;
}

.contact-icon {
   width: 40px;
   height: 30px;
   margin-top: 10px;
   color: var(--my-color);
}

.contact-input-container {
   display: flex;
   flex-direction: column;
   gap: 20px;
   margin-top: 30px;
}

.contact-input-name,
.contact-input-email,
.contact-input-message {
   padding: 12px 16px;
   font-size: 1rem;
   border: 2px solid var(--my-color);
   border-radius: 8px;
   outline: none;
   transition: border-color 0.3s ease, box-shadow 0.3s ease;
   background-color: #0a0a0a;
   color: #ffffff;
}

.contact-input-name:focus,
.contact-input-email:focus,
.contact-input-message:focus {
   border-color: var(--neon-green);
   box-shadow: 0 0 8px var(--neon-green);
}

.contact-button {
   padding: 12px 20px;
   font-size: 1.1rem;
   background-color: var(--my-color);
   color: white;
   border: none;
   border-radius: 8px;
   cursor: pointer;
   transition: background-color 0.3s ease, transform 0.2s ease;
   width: fit-content;
}

.contact-button:hover {
   background-color: var(--neon-green);
   transform: scale(1.05);
}

@media screen and (max-width: 768px) {
   .contact-container {
      width: auto;
      padding: 20px 20px;
   }

   .contact-title {
      font-size: 2rem;
      text-align: center;
      margin: 0 auto;
   }

   .contact-input-name,
   .contact-input-email,
   .contact-input-message {
      width: 80%;
      font-size: 0.95rem;
      margin: 0 auto;
   }

   .contact-button {
      width: 90%;
      text-align: center;
      margin: 0 auto;
   }
}
